import React, { Component } from 'react'
import QuizOptions from './QuizOptions';

class Quiz extends Component {
    constructor(props) {
        super(props)
        let riddle= this.playGame();

        this.state = {riddle} //riddle:riddle es6
        this.renderOptions = this.renderOptions.bind(this);
        this.checkResults = this.checkResults.bind(this);
    }

    renderOptions(){
        return(
            <div className="options">
               {this.state.riddle.resultsArray.map((option,i)=>
                   <QuizOptions option={option} key={i} checkResults={(option)=>this.checkResults(option)}/>
               )
               }
            </div>
        )
    }

    playGame(){
        let field1=this.randomNumber(30,1);
        let field2=this.randomNumber(30,1);
        let result=field1+field2;
        let resultsArray=this.generateRandomOptions(result);
        resultsArray.sort(function(a,b){return 0.5-Math.random()});
    
        let riddle ={
                resultsArray:resultsArray,
                field1: field1,
                field2: field2,
                answer: result
        }
        return riddle;
    }

    randomNumber(arg1,arg2){
        return Math.floor(Math.random() *(arg1))+arg2;
    }

    generateRandomOptions(sum){
        let result=sum;
        let resultsArray=[];
        resultsArray.push(this.randomNumber(60,1));
        resultsArray.push(this.randomNumber(60,1));
        resultsArray.push(result);
        resultsArray.push(this.randomNumber(60,1));
        return resultsArray;

    }

    checkResults(option){
        console.log("called"+option)
        if(this.state.riddle.answer === option){
            alert(`CONGRATULATIONS!!!!\nCorrect Answer`);
        }else{
            alert(`Oops!!!\nwrong answer`);
        }
    }

    rebootGame=()=>{
        let riddle= this.playGame();

        this.setState({riddle})
    };

    render() {
        return (
            <div className='quiz'>
                <div className="quiz-content">
                    <p className="question">
                    What is the sum of <span className="text-info">{this.state.riddle.field1}</span> and <span className="text-info">{this.state.riddle.field2}</span> ?
                    </p>
                    {this.renderOptions()}
                </div>
                <div className="play-again">
                    <button className="button" onClick={this.rebootGame}>Play Again</button>
                </div>
            </div>
        )
    }
}

export default Quiz;
